import moment from "moment";
import { Views } from "react-big-calendar";

export const formatTimeRange = (start, end) => {
  const startTime = moment(start).format("h");
  const endTime = moment(end).format("h A");
  return `${startTime} - ${endTime}`;
};

export const formats = {
  timeGutterFormat: (date) => moment(date).format("h A"),
  agendaTimeRangeFormat: (date) => moment(date).format("h A"),
  agendaTimeFormat: (date) => moment(date).format("h A"),
};

export const groupEventsByStartTime = (events) => {
  const grouped = {};
  events.forEach((event) => {
    const timeKey = `${
      event.start.toISOString().split("T")[0]
    }-${event.start.getHours()}:${event.start.getMinutes()}`;
    if (!grouped[timeKey]) {
      grouped[timeKey] = [];
    }
    grouped[timeKey].push(event);
  });
  return grouped;
};

export const groupEventsByDate = (events) => {
  const grouped = {};
  events.forEach((event) => {
    const dateKey = event.start.toISOString().split("T")[0];
    if (!grouped[dateKey]) {
      grouped[dateKey] = [];
    }
    grouped[dateKey].push(event);
  });
  return grouped;
};

export const formatDate = (date) => {
  const momentDate = moment(date);
  const day = momentDate.format("DD MMM");
  const weekday = momentDate.format("ddd");
  const fullweekday = momentDate.format("dddd");
  return { day, weekday, fullweekday };
};

export const calculateDateRange = (view, date) => {
  switch (view) {
    case Views.WEEK:
      return {
        start: moment(date).startOf("week").toDate(),
        end: moment(date).endOf("week").toDate(),
      };
    case Views.MONTH:
      return {
        start: moment(date).startOf("month").toDate(),
        end: moment(date).endOf("month").toDate(),
      };
    case Views.DAY:
    default:
      return {
        start: moment(date).startOf("day").toDate(),
        end: moment(date).endOf("day").toDate(),
      };
  }
};

export const formatAPIDate = (currentDate, weekOffset) => {
  const newDate = moment(currentDate).add(weekOffset, "weeks").toDate();
  const start = moment(newDate).startOf("week").toISOString();
  const end = moment(newDate).endOf("week").toISOString();
  return { start, end };
};
