import React, { useState, useEffect } from "react";
import { Calendar, momentLocalizer, Views } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import moment from "moment";
import "./CalendarComponent.css";
import CustomToolbar from "./Toolbar";
import CustomEventWrapper from "./EventWrapper";
import {
  calculateDateRange,
  formats,
  formatTimeRange,
  groupEventsByDate,
  groupEventsByStartTime,
} from "../../utils/common";
import { fetchEvents } from "../../helper/api";
import CustomHeader from "./CalenderHeader";

const localizer = momentLocalizer(moment);

const CalendarComponent = () => {
  const [events, setEvents] = useState([]);
  const [view, setView] = useState(Views.WEEK);
  const [currentDate, setCurrentDate] = useState(new Date());

  useEffect(() => {
    const { start, end } = calculateDateRange(view, currentDate);
    loadEvents(start, end);
  }, [currentDate, view]);

  const loadEvents = async (start, end) => {
    try {
      const URL = "calendar_app/api/calendar";
      const events = await fetchEvents(URL, start, end);
      setEvents(events);
    } catch (error) {
      console.error("Error loading events:", error);
    }
  };

  const handleNavigate = (date) => {
    setCurrentDate(date);
  };

  const handleViewChange = (newView) => {
    setView(newView);
  };

  const groupedEvents = groupEventsByStartTime(events);

  const filterEvents = (events) => {
    if (view === Views.WEEK) {
      const uniqueTimes = new Set();
      const filteredEvents = [];

      events.forEach((event) => {
        const timeKey = `${
          event.start.toISOString().split("T")[0]
        }-${event.start.getHours()}:${event.start.getMinutes()}`;
        if (!uniqueTimes.has(timeKey)) {
          uniqueTimes.add(timeKey);
          filteredEvents.push(event);
        }
      });

      return filteredEvents;
    } else if (view === Views.MONTH) {
      const uniqueDates = new Set();
      const filteredEvents = [];

      events.forEach((event) => {
        const dateKey = event.start.toISOString().split("T")[0];
        if (!uniqueDates.has(dateKey)) {
          uniqueDates.add(dateKey);
          filteredEvents.push(event);
        }
      });

      return filteredEvents;
    } else {
      return events;
    }
  };

  const filteredEvents = filterEvents(events);

  const groupedEventsByDate = groupEventsByDate(events);

  const getEventscount = (event) => {
    let dateKey, count;
    if (view === "month") {
      dateKey = event.start.toISOString().split("T")[0];
      count = groupedEventsByDate[dateKey]?.length || 0;
    } else {
      dateKey = `${
        event.start.toISOString().split("T")[0]
      }-${event.start.getHours()}:${event.start.getMinutes()}`;
      count = groupedEvents[dateKey]?.length || 0;
    }
    return { count };
  };

  const CustomEvent = ({ event }) => {
    const { count } = getEventscount(event);

    return (
      <div>
        {count > 1 && (view === "week" || view === "month") && (
          <div
            style={{
              position: "absolute",
              right: -10,
              top: -10,
              background: "#FED050",
              color: "#000000",
              borderRadius: "50%",
              width: 25,
              height: 25,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              fontSize: 12,
              zIndex: 10,
              fontWeight: "bold",
            }}
          >
            {count}
          </div>
        )}
        <p className="cus-p">{event.summary}</p>
        <p className="cus-p">{event.desc}</p>
        <p className="cus-p">Time: {formatTimeRange(event.start, event.end)}</p>
      </div>
    );
  };

  return (
    <div className="App">
      <h2>Your Tudo's</h2>
      <div style={{ position: "relative" }}>
        <Calendar
          localizer={localizer}
          events={filteredEvents}
          startAccessor="start"
          endAccessor="end"
          defaultView={Views.WEEK}
          views={[Views.MONTH, Views.WEEK, Views.DAY]}
          components={{
            event:
              (view === Views.WEEK ||
                view === Views.DAY ||
                view === Views.MONTH) &&
              CustomEvent,
            toolbar: (props) => (
              <CustomToolbar {...props} date={currentDate} view={view} />
            ),
            eventWrapper: (props) => (
              <CustomEventWrapper
                {...props}
                groupedEvents={
                  view === "month" ? groupedEventsByDate : groupedEvents
                }
                type={view}
              />
            ),
            header: (props) => <CustomHeader {...props} view={view} />,
          }}
          onView={handleViewChange}
          formats={formats}
          onNavigate={handleNavigate}
        />
      </div>
    </div>
  );
};

export default CalendarComponent;
