import React, { useState, useEffect } from "react";
import moment from "moment";

const CustomToolbar = ({ date, view, views, onNavigate, onView }) => {
  const [label, setLabel] = useState("");

  useEffect(() => {
    if (view === "month") {
      setLabel(
        `${date.toLocaleString("default", {
          month: "long",
        })} ${date.getFullYear()}`
      );
    } else if (view === "week") {
      const startOfWeek = moment(date).startOf("week");
      const endOfWeek = moment(date).endOf("week");
      const startLabel = startOfWeek.format("MMMM DD");
      const endLabel = endOfWeek.format("MMMM DD");
      setLabel(`${startLabel}  ${endLabel}`);
    } else if (view === "day") {
      setLabel(moment(date).format("dddd MMM DD"));
    }
  }, [date, view]);

  const goToBack = () => {
    onNavigate("PREV");
  };

  const goToNext = () => {
    onNavigate("NEXT");
  };

  const viewNamesGroup = () => {
    return views.map((name) => (
      <button
        type="button"
        key={name}
        className={view === name ? "rbc-active" : ""}
        onClick={() => onView(name)}
      >
        {name.charAt(0).toUpperCase() + name.slice(1)}
      </button>
    ));
  };

  return (
    <div className="rbc-toolbar">
      <span className="rbc-btn-group">
        <button type="button" className="cus-tb mr-5" onClick={goToBack}>
          <i className="fa-solid fa-angle-left"></i>
        </button>
        <button type="button" className="cus-tb" onClick={goToNext}>
          <i className="fa fa-angle-right" aria-hidden="true"></i>
        </button>
      </span>
      <span className="rbc-toolbar-label">{label}</span>
      <span className="rbc-btn-group">{viewNamesGroup()}</span>
    </div>
  );
};

export default CustomToolbar;
