import React, { Suspense, useState, useRef, lazy, useEffect } from "react";
import { formatTimeRange } from "../../utils/common";
const EventPopup = lazy(() => import("./EventPopup"));

const CustomEventWrapper = ({ event, children, groupedEvents, type }) => {
  const [showPopup, setShowPopup] = useState(false);
  const [showSingleEvent, setShowSingleEvent] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [popupStyle, setPopupStyle] = useState({});
  const eventRef = useRef(null);
  const popupRef = useRef(null);
  const popupEventRef = useRef(null);

  const togglePopup = (e) => {
    e.stopPropagation();
    if (type === "day") {
      handleEventClick(event);
    } else {
      setShowPopup(!showPopup);
      if (!showPopup) {
        const rect = eventRef.current.getBoundingClientRect();
        setPopupStyle({
          top: rect.top + window.scrollY + rect.height,
          left: rect.left + window.scrollX,
          zIndex: 1000,
        });
      }
    }
  };

  const handleEventClick = (event) => {
    setShowPopup(false);
    setSelectedEvent(event);
    setShowSingleEvent(true);
  };

  const handleClickOutside = (event) => {
    if (
      (popupRef.current && !popupRef.current.contains(event.target)) ||
      (popupEventRef.current &&
        !popupEventRef.current.contains(event.target) &&
        !eventRef.current.contains(event.target))
    ) {
      setShowPopup(false);
      setSelectedEvent(null);
      setShowSingleEvent(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  let eventsAtSameTime;
  if (type === "month") {
    const timeKey = event.start.toISOString().split("T")[0];
    eventsAtSameTime = groupedEvents[timeKey] || [];
  } else {
    const timeKey = `${
      event.start.toISOString().split("T")[0]
    }-${event.start.getHours()}:${event.start.getMinutes()}`;
    eventsAtSameTime = groupedEvents[timeKey] || [];
  }

  console.log(eventsAtSameTime);

  return (
    <div ref={eventRef}>
      {React.cloneElement(children, {
        onClick: togglePopup,
        className: `${children.props.className}`,
        children: (
          <div className="cus-event">
            {children.props.children}
            {showPopup && (
              <>
                {eventsAtSameTime.length > 1 ? (
                  <>
                    <div
                      className="overlay"
                      onClick={() => setShowPopup(false)}
                    ></div>
                    <div
                      className="event-card"
                      style={popupStyle}
                      ref={popupRef}
                    >
                      {eventsAtSameTime.map((ev) => (
                        <div
                          className="card"
                          key={ev.id}
                          onClick={(e) => {
                            e.stopPropagation();
                            handleEventClick(ev);
                          }}
                        >
                          <div className="rbc-event-content">
                            <p className="cus-p">{ev.summary}</p>
                            <p className="cus-p">{ev.desc}</p>
                            <p className="cus-p">
                              Time: {formatTimeRange(ev.start, ev.end)}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </>
                ) : (
                  eventsAtSameTime[0] && (
                    <Suspense
                      fallback={
                        <p className="cus-p">Event Details Loading...</p>
                      }
                    >
                      <EventPopup
                        event={eventsAtSameTime[0]}
                        eRef={popupRef}
                        style={popupStyle}
                        onClick={(e) => e.stopPropagation()}
                      />
                    </Suspense>
                  )
                )}
              </>
            )}
            {showSingleEvent && selectedEvent && (
              <Suspense
                fallback={<p className="cus-p">Event Details Loading...</p>}
              >
                <EventPopup
                  event={selectedEvent}
                  eRef={popupEventRef}
                  style={popupStyle}
                  onClick={(e) => e.stopPropagation()}
                />
              </Suspense>
            )}
          </div>
        ),
      })}
    </div>
  );
};

export default CustomEventWrapper;
