import React from "react";
import { formatDate } from "../../utils/common";

const CustomHeader = ({ date, view }) => {
  const jsDate = new Date(date);
  const { day, weekday, fullweekday } = formatDate(jsDate);

  return (
    <div>
      {view === "week" && <div className="pb-5 fw-b">{day}</div>}
      <div className="fw-b">{view === "week" ? fullweekday : weekday}</div>
      {view === "day" && <div className="fw-b">Hello</div>}
    </div>
  );
};

export default CustomHeader;
