import moment from "moment";
import axiosInstance from "../utils/axiosInstance";
import axios from "axios";

const formatDate = (date) => moment(date).format("YYYY-MM-DD");

export const fetchEvents = async (url, start, end) => {
  try {
    const response = await axiosInstance.get(url, {
      params: {
        from_date: formatDate(start),
        to_date: formatDate(end),
      },
    });
    const formattedEvents = response.data.map((event) => ({
      ...event,
      title: event.summary,
      start: new Date(event.start),
      end: new Date(event.end),
    }));

    return formattedEvents;
  } catch (error) {
    const response = await axios.get("./sample.json");
    const formattedEvents = response.data.map((event) => ({
      ...event,
      title: event.summary,
      start: new Date(event.start),
      end: new Date(event.end),
    }));
    return formattedEvents;
    // console.error("Error fetching events:", error);
    // throw error;
  }
};

export const fetchEvent = async (url, id) => {
  try {
    const response = await axiosInstance.get(url, {
      params: { id },
    });
    return response.data;
  } catch (error) {
    const response = await axios.get("./sample2.json");
    return response.data;
    // console.error("Error fetching event:", error);
    // throw error;
  }
};
